import React from 'react'
import {connect} from 'react-redux'

import { openAddStudent } from '../../redux/modal/modal.actions'

function CustomButtom({ buttonText, kind, openAddStudent }) {

  function handleClick () {
    switch (kind){
      case "addStudent":
        openAddStudent()
        break;
      default:
        break;
    }
  }

  return (
      <button onClick={() => handleClick()} type="button" className="btn btn-primary btn-lg">{ buttonText }</button>
  )
}

const mapDispatchToProps = dispatch => ({
  openAddStudent: action => dispatch(openAddStudent())
})

export default connect(null, mapDispatchToProps)(CustomButtom)

