const INITIAL_STATE = {
  students: [],
  sortOptions: {
    "name": true,
    "docNum": false,
    "grade": false,
    "releaseDate": false
  },
  sortAsc: true
}

const studentsReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case 'GET_CLASSROOM':
      return {
        ...state,
        students: action.payload
      }
    case 'SORT_CLASSROOM':
      return {
        ...state,
        students: action.payload
      }
    case 'SORT_BY':
      return {
        ...state,
        sortOptions: action.payload
      }
    case 'SORT_ASC_TOGGLE':
      return {
        ...state,
        sortAsc: action.payload
      }

    default:
      return state;
  }
}

export default studentsReducer