import React from 'react'
import { connect } from 'react-redux'

import { closeAddStudent} from '../../redux/modal/modal.actions'

import SignInBody from './sign-in-body/sign-in-body.component'

function Modal({ title, closeAddStudent }) {

  function handleClick () {
    closeAddStudent()
  }

  return (
    <div className="modal">
      <div className="modal-dialog" role="document">
        <div className="modal-content">
          <div className="modal-header">
            <h5 className="modal-title">{title}</h5>
            <button onClick={() => handleClick()} type="button" className="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <SignInBody></SignInBody>
        </div>
      </div>
    </div>
  )
}

const mapDispatchToProps = dispatch => ({
  closeAddStudent: action => dispatch(closeAddStudent())
})

export default connect(null, mapDispatchToProps)(Modal);
