import React from 'react'
import { connect } from 'react-redux'

import OptionsPanel from './components/options-panel/options-panel.component'
import DataDisplay from './components/data-display/data-display.component'
import Modal from './components/modal/modal.component'

import { getClassroom } from './redux/students/students.actions'

import './App.css'

class App extends React.Component {
  // Get the data
  // Replace this with a call to a database
  componentDidMount = () => {
    fetch('./assets/student.data.json')
      .then(res => res.json())
      .then(
        (studentData) => {
          this.props.getClassroom(studentData)
        }, (error) => { console.log() }
      )
  }

  render() {
    return (
      <div className="App">
        <OptionsPanel />
        <DataDisplay />
        {
          this.props.showAddStudent ? <Modal title="Add Student" kind="addStudent"/> : null
        }
      </div>
    );
  }
}

const mapStateToProps = state => ({
  showAddStudent: state.modal.showAddStudent
})

const mapDispatchToProps = dispatch => ({
  getClassroom: classroom => dispatch(getClassroom(classroom))
})

export default connect(mapStateToProps, mapDispatchToProps)(App);
