export const getClassroom = classroom => ({
  type: 'GET_CLASSROOM',
  payload: classroom
})

export const sortClassroom = classroom => ({
  type: 'SORT_CLASSROOM',
  payload: classroom
})

export const sortBy = sortOption => ({
  type: 'SORT_BY',
  payload: sortOption
})

export const sortAscToggle = sortAsc => ({
  type: 'SORT_ASC_TOGGLE',
  payload: sortAsc
})