import React from 'react'
import { connect } from 'react-redux'

import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowUp, faArrowDown, faDog, faDownload } from '@fortawesome/free-solid-svg-icons'

import { sortClassroom, sortBy, sortAscToggle } from '../../redux/students/students.actions'


import './table-head.styles.scss'

const TableHead = ({ students, sortClassroom, sortBy, sortOptions, sortAsc, sortAscToggle }) => {

  const handleSort = (sortingOption) => {
    let sortedClassroom = [...students]
    if (sortAsc) {
      sortedClassroom.sort((a, b) => a[sortingOption] > b[sortingOption] ? 1 : a[sortingOption] < b[sortingOption] ? -1 : 0)
      sortAscToggle(false)
    } else {
      sortedClassroom.sort((a, b) => a[sortingOption] < b[sortingOption] ? 1 : a[sortingOption] > b[sortingOption] ? -1 : 0)
      sortAscToggle(true)
    }

    function updateSort(opt){
      let returnOb = {};
      Object.keys(sortOptions).forEach(
        e => e === opt ? (returnOb[e] = true) : (returnOb[e] = false)
      )
      return returnOb
    }

    sortBy(updateSort(sortingOption))
    sortClassroom(sortedClassroom)
  }

  return (
    <thead>
      <tr>
        <th scope="col" onClick={() => handleSort("name")}>
          Name
          <span className="sortIcon">
            {
              sortOptions.name && sortAsc ? <FontAwesomeIcon icon={faArrowUp} /> :
              sortOptions.name && !sortAsc ? <FontAwesomeIcon icon={faArrowDown} /> : null
            }
          </span>
        </th>
        <th scope="col" onClick={() => handleSort("docNum")}>
          DOC Number
          <span className="sortIcon">
          {
              sortOptions.docNum && sortAsc ? <FontAwesomeIcon icon={faArrowUp} /> :
              sortOptions.docNum && !sortAsc ? <FontAwesomeIcon icon={faArrowDown} /> : null
            }
          </span>
        </th>
        <th scope="col" onClick={() => handleSort("grade")}>Grade</th>
        <th scope="col" onClick={() => handleSort("release")}>Release Date</th>
      </tr>
    </thead>
  )
}

const mapStateToProps = state => ({
  students: state.students.students,
  sortOptions: state.students.sortOptions,
  sortAsc: state.students.sortAsc
})

const mapDispatchToProps = dispatch => ({
  sortClassroom: classroom => dispatch(sortClassroom(classroom)),
  sortBy: sortOption => dispatch(sortBy(sortOption)),
  sortAscToggle: sortAsc => dispatch(sortAscToggle(sortAsc)) 
})

export default connect(mapStateToProps, mapDispatchToProps)(TableHead)
