const INITIAL_STATE = {
  showAddStudent: false
}

const modalReducer = (state = INITIAL_STATE, action) => {
  switch (action.type) {
    case 'OPEN_ADD_STUDENT':
      return {
        ...state,
        showAddStudent: true
      }
    case 'CLOSE_ADD_STUDENT':
      return {
        ...state,
        showAddStudent: false
      }
    default:
      return state;
  }
}

export default modalReducer